import React, { useEffect, useMemo, useState } from 'react';
import './UserGraph.scss';
import cx from "classnames";
import { ImSpinner2 } from 'react-icons/im';
import * as ChartModule from "chart.js/auto";
import { Line } from 'react-chartjs-2';
import zoom from 'chartjs-plugin-zoom';
import 'chartjs-adapter-date-fns';
import { daysToMs, findLatestOrEarliest } from '../../../../components/utils/utils';
import { eachDayOfInterval, format } from 'date-fns';
import { ALL_MARKETS } from '../../../../components/utils/constants';
import { CHART_COLORS } from '../../analyticsConstants';
import { floor } from '../../../../components/utils/math';

const UserGraph = (props) => {
	const {
		txData,
		isLoading,
	} = props;

	const [profitPerps, profitOptions, profitTotal] = useMemo(() => {
		if (txData.length) {
			const profitPerps = txData.reduce((profit, tx) => {
				if (tx.market === "GMX" || tx.market === "GNS") {
					return {
						sum: profit.sum + tx.amount_usd,
						outflow: tx.amount_usd >= 0 ? profit.outflow : profit.outflow + tx.amount_usd
					}
				} else {
					return profit;
				}
			}, {
				sum: 0,
				outflow: 0
			});
			profitPerps.percentage = profitPerps.sum / profitPerps.outflow * 100;
			
			const profitOptions = txData.reduce((profit, tx) => {
				if (tx.market === "Hegic" || tx.market === "Lyra") {
					return {
						sum: profit.sum + tx.amount_usd,
						outflow: tx.amount_usd >= 0 ? profit.outflow : profit.outflow + tx.amount_usd
					}
				} else {
					return profit;
				}
			}, {
				sum: 0,
				outflow: 0
			});
			profitOptions.percentage = profitOptions.sum / profitOptions.outflow * 100;
			
			const profitTotal = {
				sum: profitPerps.sum + profitOptions.sum,
				outflow: profitPerps.outflow + profitOptions.outflow,
			};
			profitTotal.percentage = profitTotal.sum / profitTotal.outflow * 100;
	
			return [profitPerps, profitOptions, profitTotal];
		}
		else return [];
	}, [txData]);
	
	const profitPerpsSign = profitPerps?.sum < 0 ? "" : "";
	const profitOptionsSign = profitOptions?.sum < 0 ? "" : "";
	const profitTotalSign = profitTotal?.sum < 0 ? "" : "";

	const [chartData, setChartData] = useState(null);
	const Chart = ChartModule.default;
	Chart.register(zoom);
	const [dateRange, setDateRange] = useState(null);
	const [profitRange, setProfitRange] = useState(null);
	useEffect(() => {
		if (txData.length) {
			const earliestDate = new Date(findLatestOrEarliest(txData, "block_timestamp", false).chart_timestamp);
			const latestDate = new Date(findLatestOrEarliest(txData, "block_timestamp", true).chart_timestamp);
			setDateRange({
				inView: [latestDate.getTime() - daysToMs(7), Date.now()],
				limits: [earliestDate.getTime(), Date.now()],
			});
			const dates = eachDayOfInterval({
				start: earliestDate,
				end: latestDate,
			});
			const labels = dates.map(date => {
				return (new Date(date)).getTime();
			})

			const datasets = [];
			for (let i = 0; i < ALL_MARKETS.length; i++) {
				const market = ALL_MARKETS[i];
				const dataset = {
					label: market,
					borderColor: CHART_COLORS[market],
					backgroundColor: CHART_COLORS[market],
					pointBackgroundColor: CHART_COLORS[market],
					hoverBackgroundColor: CHART_COLORS[market],
					hoverBorderColor: CHART_COLORS[market],
					tooltip: {
						callbacks: {
							labelColor: () => {
								return {
									backgroundColor: CHART_COLORS[market],
									borderColor: CHART_COLORS[market],
								}
							},
						}
					},
					data: [],
				}
				
				for (let j = 0; j < dates.length; j++) {
					const date = dates[j];
					const dateTxs = txData.filter(tx => {
						return ((new Date(tx.chart_timestamp)).getTime() === date.getTime()) && (tx.market === market)
					});
					const profit = dateTxs.reduce((profit, tx) => {
						return profit + tx.amount_usd;
					}, 0);

					dataset.data.push((profit && floor(profit, 2)) || undefined);
				}

				datasets.push(dataset);
			}

			const maxProfit = datasets.map(set => {
				return set.data.reduce((prev, cur) => {
					if (cur > prev) {
						return cur;
					}
					return prev;
				}, 0);
			}).reduce((prev, cur) => {
				if (cur > prev) {
					return cur;
				}
				return prev;
			}, 0);
			const maxLoss = datasets.map(set => {
				return set.data.reduce((prev, cur) => {
					if (cur < prev) {
						return cur;
					}
					return prev;
				}, 0);
			}).reduce((prev, cur) => {
				if (cur < prev) {
					return cur;
				}
				return prev;
			});

			setProfitRange({
				inView: [-100, 100],
				limits: [maxProfit, maxLoss]
			});
			
			const chartData = {
				labels,
				datasets,
			}
			setChartData(chartData);
		}
	}, [txData]);

	const defaultFont = {
		family: "'Gantari', sans-serif",
		weight: 500,
	};
	const globalOptions = {
		spanGaps: true,
		pointStyle: 'circle',
		pointRadius: 0,
		pointHoverRadius: 4,
		pointHitRadius: 8,
		borderWidth: 2,
		interaction: {
			mode: 'x',
			intersect: true
		},
		maintainAspectRatio: false,
	}
	const axesOptions = {
		grid: {
			display: false,
		},
	};
	const ticksOptions = {
		font: {
			...defaultFont,
			size: 14,
		},
		color: '#384263',
	}
	const legendOptions = {
		position: 'bottom',
		align: 'end',
		labels: {
			usePointStyle: true,
			pointStyleWidth: 9,
			boxHeight: 6,
			boxWidth: 6,
			color: '#fff',
			font: {
				...defaultFont,
				size: 15,
			},
			padding: 20,
		},
	};
	const zoomOptions = {
		zoom: {
			enabled: true,
			mode: 'xy',
			wheel: {
				enabled: true
			},
		},
		pan: {
			enabled: true
		},
		pinch: {
			enabled: true
		},
		limits: {
			x: {
				min: dateRange && dateRange.limits[0],
				max: dateRange && dateRange.limits[1],
			},
			// y: {
			// 	min: profitRange && profitRange.limits[0],
			// 	max: profitRange && profitRange.limits[1],
			// }
		},
	};
	const tooltipOptions = {
		titleColor: '#3A4466',
		titleFont: {
			...defaultFont,
			size: 12,
		},
		position: 'nearest',
		usePointStyle: true,
		boxWidth: 6,
		borderWidth: 1,
		borderColor: 'rgba(56, 66, 99, 0.2)',
		backgroundColor: '#141637',
		cornerRadius: 5,
		padding: 8,
		caretPadding: 5,
		boxPadding: 4,
		xAlign: 'left',
		yAlign: 'top',
	};
	const pluginsOptions = {
		legend: {
			...legendOptions,
		},
		zoom: {
			...zoomOptions,
		},
		tooltip: {
			...tooltipOptions,
		}
	};
	return (
		<div className="UserGraph">
			<h1>History</h1>
			<div className="UserGraph__content">
				<div className="UserGraph__graph App-box">
					<div className="UserGraph__graph-canvas">
						{chartData && (
							<Line
								datasetIdKey='txData'
								options={{
									...globalOptions,
									scales: {
										x: {
											...axesOptions,
											type: 'time',
											time: {
												minUnit: 'day',
												displayFormats: {
													day: 'd MMM'
												},
												tooltipFormat: 'd MMM yyyy',
											},
											ticks: {
												...ticksOptions,
											},
											min: dateRange.inView[0],
											max: dateRange.inView[1],
										},
										y: {
											...axesOptions,
											position: 'right',
											ticks: {
												...ticksOptions,
												callback: (value) => {
													let sign = '';
													if (value < 0) {
														sign = '-';
													}
													return `${sign}$${Math.abs(value).toFixed(0)}`;
												}
											},
											suggestedMin: profitRange.inView[0],
											suggestedMax: profitRange.inView[1],
										}
									},
									plugins: {
										...pluginsOptions,
									}
								}}
								data={chartData}
							/>
						)}
					</div>
				</div>
				<div className="UserGraph__overview App-box">
					<h2>Overview</h2>
					<div className="UserGraph__overview-items">
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Realized PnL (Perps)
							</div>
							<div className="UserGraph__overview-item-pnl">
								{profitPerps !== undefined && (
									<>
										{profitPerpsSign}${Math.abs(profitPerps.sum).toFixed(2)}
										<span
											className={cx('UserGraph__overview-item-pnl-percentage', { 
												positive: profitPerps?.sum > 0,
												negative: profitPerps?.sum < 0,
												muted: profitPerps?.sum === 0,
											})}>
												({profitPerpsSign || '+'}{(isNaN(profitPerps.percentage) ? 0 : Math.abs(profitPerps.percentage)).toFixed(0)}%)
										</span>
									</>
								)}
								
								{isLoading && <ImSpinner2 className="spin position-loading-icon" />}
							</div>
						</div>
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Realized PnL (Options)
							</div>
							<div className="UserGraph__overview-item-pnl">
								{profitOptions !== undefined && (
									<>
										{profitOptionsSign}${Math.abs(profitOptions.sum).toFixed(2)}
										<span
											className={cx('UserGraph__overview-item-pnl-percentage', { 
												positive: profitOptions?.sum > 0,
												negative: profitOptions?.sum < 0,
												muted: profitOptions?.sum === 0,
											})}>
												({profitOptionsSign || '+'}{((isNaN(profitOptions.percentage) ? 0 : Math.abs(profitOptions.percentage))).toFixed(0)}%)
										</span>
									</>
								)}
								{isLoading && <ImSpinner2 className="spin position-loading-icon" />}
							</div>
						</div>
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Total Realized PnL
							</div>
							<div className="UserGraph__overview-item-pnl">
								{profitTotal !== undefined && (
									<>
										{profitTotalSign}${Math.abs(profitTotal.sum).toFixed(2)}
										<span
											className={cx('UserGraph__overview-item-pnl-percentage', { 
												positive: profitTotal?.sum > 0,
												negative: profitTotal?.sum < 0,
												muted: profitTotal?.sum === 0,
											})}>
												({profitTotalSign || '+'}{(isNaN(profitTotal.percentage) ? 0 : Math.abs(profitTotal.percentage)).toFixed(0)}%)
										</span>
									</>
								)}
								{isLoading && <ImSpinner2 className="spin position-loading-icon" />}
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default UserGraph;